	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"PhD student at National Taiwan University, research interests cover Deep Learning, Natural Language Processing, and dialogue systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/ACL2019-DSL/index.md absPath of file >>> MarkdownRemark","html":"<p><b>This paper is published in Proceedings of The 57th Annual Meeting of the Association for Computational Linguistics (ACL2019).</b></p>\n<p>Full paper:\n<a href=\"/ACL_2019___Dual_Supervised_Learning2-ab700554e0ebde63ca74abf3a393d37e.pdf\" target=\"_blank\">Here</a>,\n<a href=\"https://arxiv.org/abs/1905.06196\" target=\"_blank\">arXiv</a></p>\n<p>Natural language understanding (NLU) and natural language generation (NLG) are both critical research topics in the NLP field.\nNatural language understanding is to extract the core semantic meaning from the given utterances, while natural language generation is opposite, of which the goal is to construct corresponding sentences based on the given semantics.\nHowever, such dual relationship has not been investigated in the literature.\nThis paper proposes a new learning framework for language understanding and generation on top of dual supervised learning, providing a way to exploit the duality.\nThe preliminary experiments show that the proposed approach boosts the performance for both tasks. </p>","fields":{"tagSlugs":["/tags/nlu/","/tags/nlg/","/tags/dialogue/"]},"frontmatter":{"title":"Dual Supervised Learning for Natural Language Understanding and Generation","tags":["NLU","NLG","Dialogue"],"date":"2019-05-17T22:40:32.169Z","description":"Shang-Yu Su, Chao-Wei Huang, and Yun-Nung Chen","conference":"ACL2019"}}},"pathContext":{"slug":"/posts/acl2019-dsl/"}}

